___CSS_LOADER_EXPORT___.push([module.id, `.video-card-container {
  margin: 23px 0 0 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 24px 22px;
}

.v-p {
  display: flex;
  flex-direction: row;
  gap: 0;
}
.v-p__title {
  color: var(--3, #fff);
  font-family: Uto-500;
  font-size: 24px;
}
.v-p__container {
  width: 100%;
  margin: 24px 0 0 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.v-p__button {
  border-radius: 8px;
  background: var(--10, #111);
  padding: 13px 24px;
  color: var(--5, #bbb);
  font-family: Uto-500;
  font-size: 24px;
  cursor: pointer;
}
.v-p__button.active {
  border-radius: 8px;
  background: var(--9, #222);
  color: var(--3, #fff);
  font-family: Uto-500;
  font-size: 24px;
}
.v-p__button-mobile {
  display: none;
}
.v-p__orange-button {
  margin: 7px 0 0 0;
}

.sort {
  margin: 31px 0 0 0;
  display: flex;
  flex-direction: row;
  width: 100%;
  border-bottom: 1px solid var(--9, #222);
}
.sort__button {
  display: flex;
  padding: 8px 24px;
  justify-content: center;
  align-items: center;
  color: var(--7, #777);
  text-align: center;
  font-family: Uto-600;
  font-size: 16px;
  cursor: pointer;
}
.sort__button.active {
  border-bottom: 1px solid var(--light, #ad7955);
  color: var(--3, #fff);
  text-align: center;
  font-family: Uto-600;
  font-size: 16px;
}

@media (max-width: 650px) {
  .video-card-container {
    margin: 16px 0 0 0;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    gap: 16px 21px;
  }
  .v-p {
    display: flex;
    flex-direction: row;
    gap: 0;
  }
  .v-p__container {
    width: 100%;
    margin: 16px 0 0 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .v-p__button {
    border-radius: 8px;
    background: var(--10, #111);
    padding: 12px 16px;
    color: var(--5, #bbb);
    font-family: Uto-500;
    font-size: 16px;
  }
  .v-p__button.active {
    border-radius: 8px;
    background: var(--9, #222);
    color: var(--3, #fff);
    font-family: Uto-500;
    font-size: 16px;
  }
  .v-p__orange-button {
    display: none;
  }
  .v-p__button-mobile {
    width: 54px;
    height: 54px;
    border-radius: 8px;
    background: var(--light, #ad7955);
    position: fixed;
    bottom: 34px;
    right: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 20;
  }
  .sort {
    margin: 7px 0 0 32px;
    display: flex;
    flex-direction: row;
    width: 100%;
    border-bottom: 1px solid var(--9, #222);
  }
  .sort__button {
    display: flex;
    padding: 7px 37px;
    justify-content: center;
    align-items: center;
    color: var(--7, #777);
    text-align: center;
    font-family: Uto-600;
    font-size: 16px;
    cursor: pointer;
  }
  .sort__button.active {
    border-bottom: 1px solid var(--light, #ad7955);
    color: var(--3, #fff);
    text-align: center;
    font-family: Uto-600;
    font-size: 16px;
  }
