___CSS_LOADER_EXPORT___.push([module.id, `.playlist-view {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 0 0 18px 0;
  max-width: 920px;
}
.playlist-view__info {
  margin: 27.5px 0 0 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.playlist-view__video-number {
  color: var(--3, #fff);
  font-family: Uto-500;
  font-size: 16px;
}
.playlist-view__views-time {
  color: #bbb;
  font-family: Uto-300;
  font-size: 16px;
  display: flex;
  flex-direction: row;
  gap: 16px;
}
.playlist-view__description {
  margin: 27.5px 0 0 0;
  color: #eee;
  font-family: Uto-400;
  font-size: 14px;
}
.playlist-view__description span {
  color: var(--light, #ad7955);
  font-family: Uto-400;
  font-size: 14px;
  text-decoration-line: underline;
  cursor: pointer;
}
.playlist-view__videos {
  margin: 32px 0 0 0;
  display: flex;
  justify-content: center;
  width: 100%;
  background: var(--9, #222);
  height: 100%;
  padding: 24px;
}
.playlist-view__videos-container {
  width: 100%;
  max-width: 1394px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  gap: 21.64px;
}

@media (max-width: 650px) {
  .playlist-view {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 0 16px;
  }
  .playlist-view__info {
    margin: 26px 0 0 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 1px;
  }
  .playlist-view__video-number {
    color: var(--3, #fff);
    font-family: Uto-500;
    font-size: 16px;
  }
  .playlist-view__views-time {
    color: #bbb;
    font-family: Uto-300;
    font-size: 16px;
    display: flex;
    flex-direction: row;
    gap: 8px;
  }
  .playlist-view__description {
    margin: 27.5px 0 28px 0;
    color: #eee;
    font-family: Uto-400;
    font-size: 14px;
  }
  .playlist-view__description span {
    color: var(--light, #ad7955);
    font-family: Uto-400;
    font-size: 14px;
    text-decoration-line: underline;
    cursor: pointer;
  }
  .playlist-view__videos {
    margin: 32px 0 0 0;
    display: flex;
    justify-content: center;
    width: 100%;
    background: var(--9, #222);
    height: 100%;
    padding: 24px;
  }
  .playlist-view__videos-container {
    width: 100%;
